body {
    font-family: Arial, sans-serif;
    text-align: center;
    margin: 0;
    width: 100%;
    height: 100%;
    background: rgb(17, 0, 63);
}

#network {
    margin: auto;
    border: 1px solid #000000;
    background-color: #9a9a9a;
    border-radius: 5px;
    max-width: 1275px;
}

.title h1 , .title h2 {
    margin-bottom: 0;
}

.title h1 {
    font-size: 50px;
}
.centrality-container h6 {
    font-size: 13px;
    text-align: center;
    margin-bottom: 0;
    margin-right: 5px;
}

.node-sizing-container h4 {
    margin: auto;
    text-align: center;
}


.node-label {
    fill: rgb(246,184,19);
    -webkit-text-stroke: 1px black;
    color: white;
    opacity: 1;
    text-shadow:
        1px 1px 0 #000,
        -1px -1px 0 #000,  
        1px -1px 0 #000,
        -1px 1px 0 #000,
        1px 1px 0 #000;
}

.node-label.dimmed {
    opacity: 1;
    display: block;
    fill: rgba(255, 153, 0, 0.213);
}

.control-group {
    display: flex;
    color: rgb(255, 255, 255);
    flex-direction: column;
    align-items: center;
    padding: 0;
    margin-bottom: 0;
}

.control-group label {
    font-weight: 500;
    margin-bottom: 0;
    margin-right: 5px;
}


.controls-sidebar h3 {
    margin: auto;
    margin-top: 0;
    color: rgb(255, 255, 255);
    margin-bottom: 10px;
}

.by-line {
    font-size: small;
}

.by-line a {
    color: rgb(246,184,19);
}

.network-svg {
    display: inline-block;
    width: fit-content;
}

.middle-bit {
    background-color: rgb(5,155,216);
    border-radius: 10px;
}

.title {
    text-align: center;
    background-color: rgb(5,155,216);
    color: rgb(243,243,243);
    padding: 2px;
    border-radius: 10px;
}

.title h2 {
    color: rgb(246,184,19);
}

line {
    stroke-width: 5px;
    stroke: #000000;
    opacity: 1;
}

line.dimmed {
    opacity: 1;
    stroke: #0000002a;
    stroke-width: 1px;
}

circle {
    stroke-width: 5px;
    stroke: #000000;
    fill: rgb(5,155,216);
    cursor: pointer;
    opacity: 1;
}

circle.highlighted {
    fill: rgb(0, 0, 255);
    opacity: 1;
}

circle.selected {
    fill: rgb(186,34,22);
    opacity: 1;
}

circle.dimmed {
    opacity: 1;
    fill: rgba(0, 183, 255, 0.201);
}

.slider-range-container {
    border: 0 solid black;
    padding: 10px;
    border-radius: 5px;
}

.slider-range-container h6 {
    font-size: 13px;
    text-align: center;
    margin-bottom: 5px;
    margin-right: 5px;
}

.centrality-container {
    border: 0 solid black;
    padding: 10px;
    border-radius: 5px;
}

