    margin-top: 0;
    margin-bottom: 0;
    justify-content: center;
    align-items: center;
}

.node-sizing-container hr {
    margin: 0;
}

.sidebar {
    border-radius: 5px;
    position: absolute;
    right: 0;
    top: 0;
    width: 300px;
    min-width: 175px;
    height: 100%;
    background-color: #f4f4f4;
    border-left: 2px solid #ccc;
    padding: 15px;
    overflow-y: auto;  /* Enables scrolling if the content is taller than the sidebar */
    display: none;  /* Hide by default */
    resize: horizontal;
    overflow: auto;
    text-align: center;
