 auto;\r\n    text-align: center;\r\n}\r\n\r\n\r\n.node-label {\r\n    fill: rgb(246,184,19);\r\n    -webkit-text-stroke: 1px black;\r\n    color: white;\r\n    opacity: 1;\r\n    text-shadow:\r\n        1px 1px 0 #000,\r\n        -1px -1px 0 #000,  \r\n        1px -1px 0 #000,\r\n        -1px 1px 0 #000,\r\n        1px 1px 0 #000;\r\n}\r\n\r\n.node-label.dimmed {\r\n    opacity: 1;\r\n    display: block;\r\n    fill: rgba(255, 153, 0, 0.213);\r\n}\r\n\r\n.control-group {\r\n    display: flex;\r\n    color: rgb(255, 255, 255);\r\n    flex-direction: column;\r\n    align-items: center;\r\n    padding: 0;\r\n    margin-bottom: 0;\r\n}\r\n\r\n.control-group label {\r\n    font-weight: 500;\r\n    margin-bottom: 0;\r\n    margin-right: 5px;\r\n}\r\n\r\n\r\n.controls-sidebar h3 {\r\n    margin: auto;\r\n    margin-top: 0;\r\n    color: rgb(255, 255, 255);\r\n    margin-bottom: 10px;\r\n}\r\n\r\n.by-line {\r\n    font-size: small;\r\n}\r\n\r\n.by-line a {\r\n    color: rgb(246,184,19);\r\n}\r\n\r\n.network-svg {\r\n    display: inline-block;\r\n    width: fit-content;\r\n}\r\n\r\n.middle-bit {\r\n    background-color: rgb(5,155,216);\r\n    border-radius: 10px;\r\n}\r\n\r\n.title {\r\n    text-align: center;\r\n    background-color: rgb(5,155,216);\r\n    color: rgb(243,243,243);\r\n    padding: 2px;\r\n    border-radius: 10px;\r\n}\r\n\r\n.title h2 {\r\n    color: rgb(246,184,19);\r\n}\r\n\r\nline {\r\n    stroke-width: 5px;\r\n    stroke: #000000;\r\n    opacity: 1;\r\n}\r\n\r\nline.dimmed {\r\n    opacity: 1;\r\n    stroke: #0000002a;\r\n    stroke-width: 1px;\r\n}\r\n\r\ncircle {\r\n    stroke-width: 5px;\r\n    stroke: #000000;\r\n    fill: rgb(5,155,216);\r\n    cursor: pointer;\r\n    opacity: 1;\r\n}\r\n\r\ncircle.highlighted {\r\n    fill: rgb(0, 0, 255);\r\n    opacity: 1;\r\n}\r\n\r\ncircle.selected {\r\n    fill: rgb(186,34,22);\r\n    opacity: 1;\r\n}\r\n\r\ncircle.dimmed {\r\n    opacity: 1;\r\n    fill: rgba(0, 183, 255, 0.201);\r\n}\r\n\r\n.slider-range-container {\r\n    border: 0 solid black;\r\n    padding: 10px;\r\n    border-radius: 5px;\r\n}\r\n\r\n.slider-range-container h6 {\r\n    font-size: 13px;\r\n    text-align: center;\r\n    margin-bottom: 5px;\r\n    margin-right: 5px;\r\n}\r\n\r\n.centrality-container {\r\n    border: 0 solid black;\r\n    padding: 10px;\r\n    border-radius: 5px;\r\n}\r\n\r\n/* .centrality-container div {\r\n    display: block;\r\n    align-items: center;\r\n    justify-content: center;\r\n} */\r\n\r\n.heading-info-question {\r\n    display: flex;\r\n    flex-direction: row;\r\n    align-items: center;\r\n    justify-content: center;\r\n    text-align: center;\r\n    margin-bottom: 5px;\r\n}\r\n\r\n.circled-icon {\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    border: 1px solid black;\r\n    border-radius: 100%;\r\n}\r\n\r\n.centrality-container div label {\r\n    margin-left: 5px;\r\n}\r\n\r\nhr {\r\n    display: block;\r\n    margin: auto;\r\n    margin-bottom: 0px;\r\n    padding: 0;\r\n}\r\n.sidebar hr {\r\n    border-top: 3px solid #ccc;\r\n    padding: 5px;\r\n    margin: auto;\r\n    margin-bottom: 0px;\r\n    width: 100%;\r\n}\r\n\r\n.controls-sidebar hr {\r\n    border-top: 3px solid rgb(186,34,22);\r\n    margin-top: 12px;\r\n    margin-bottom: 12px;\r\n}\r\n\r\n\r\n.node-sizing-container {\r\n    color: rgb(255, 255, 255);\r\n    margin: auto;\r\n    margin-top: 0;\r\n    margin-bottom: 0;\r\n    justify-content: center;\r\n    align-items: center;\r\n}\r\n\r\n.node-sizing-container hr {\r\n    margin: 0;\r\n}\r\n\r\n.sidebar {\r\n    border-radius: 5px;\r\n    position: absolute;\r\n    right: 0;\r\n    top: 0;\r\n    width: 300px;\r\n    min-width: 175px;\r\n    height: 100%;\r\n    background-color: #f4f4f4;\r\n    border-left: 2px solid #ccc;\r\n    padding: 15px;\r\n    overflow-y: auto;  /* Enables scrolling if the content is taller than the sidebar */\r\n    display: none;  /* Hide by default */\r\n    resize: horizontal;\r